<template>
  <div>
    <div class="background" v-if="imgSrcShow">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
      <div
        style="position: absolute;bottom: 2.5rem;left: 19.5rem;width: 3rem;height: 2rem"
        @click="dataWeChang"
      ></div>
    </div>
    <div class="background" v-if="!imgSrcShow">
      <img :src="imgSrcTow" width="100%" height="100%" alt="" />
      <div
        style="position: absolute;bottom: 7.4rem;left: 2.7rem; width: 100%;height: 3.3rem;"
        @click="getDetails"
      ></div>
      <div
        style="position: absolute;bottom: 2.5rem;left: 13.5rem;width: 3rem;height: 2rem"
        @click="dataMoChang"
      ></div>
    </div>
    <div class="front" v-if="imgSrcShow">
      <navigationBar :timeValue="timeValue" @operationChangeVlaue="operationChangeVlaue" v-if="operationShow"></navigationBar>
      <div class="tab-top">
        <van-icon
          @click="operationShow = true"
          name="wap-nav"
          style="align-items: center;justify-content: center;display: flex;"
        />
        <span style="font-family: regular;font-weight: 400;"
          >{{ timeValue }}
        </span>
      </div>
      <div class="content">
        <div class="day">
          <div class="date">{{ date }}</div>
          <div class="fg"><span>/</span></div>
          <div class="ez">
            <span>{{ month }}</span>
            <span>{{ year }}</span>
          </div>
        </div>
        <div class="button">
          <van-button
            icon="arrow-up"
            type="info"
            @click.stop="next('pageUp')"
          />
          <div class="monthVal">{{ monthVal }}</div>
          <van-button
            icon="arrow-down"
            type="info"
            @click.stop="next('pageDown')"
          />
        </div>
      </div>
      <div class="imgList">
        <div
          style="position: absolute;top: 30.3rem;left: 1.3rem;width: 106px;height: 136px;"
          @click="double"
        ></div>
        <img
          src="@/assets/12123.png"
          style="display: block;position: absolute;top: 30.3rem;left: 1.3rem;"
          v-if="hdShow"
        />

        <van-action-sheet
          :overlay="false"
          v-model="hdShowBox"
          style="display: block;position: fixed;bottom: 0;"
        >
          <img src="@/assets/1212.png" @click.stop="hdShowBox = false" />
        </van-action-sheet>
        <div
          style="position: absolute;top: 36rem;right: 1.3rem;;width: 106px;height: 136px;"
          @click="christmas"
        ></div>
        <img
          src="@/assets/3.png"
          style="display: block;position: absolute;top: 36rem;right: 1.3rem;"
          v-if="sdShow"
        />
        <van-action-sheet
          :overlay="false"
          v-model="sdShowBox"
          style="display: block;position: fixed;bottom: 0;"
        >
          <img src="@/assets/1.png" @click.stop="sdShowBox = false" />
        </van-action-sheet>
      </div>
    </div>
    <div class="overlay" style="position: absolute;z-index: 1;" v-if="overlayShow">
      <div>
        <img :src="overlaySrc" width="100%" height="100%" alt="">
      </div>
      <div style="position: absolute;top: 0rem;">
        <img :src="overlayLoadSrc" alt="">
      </div>
    </div>
  </div>
</template>

<script>
import navigationBar from "../components/navigationBar.vue"
export default {
  name: "Home",
  components:{ navigationBar },
  data() {
    return {
      timeValue: "",
      date: "",
      month: "",
      year: "",
      monthVal: "本月",
      imgSrc: require("@/assets/93.jpg"),
      imgSrcTow: require("@/assets/95.jpg"),
      hdShow: false,
      sdShow: false,
      hdShowBox: false,
      sdShowBox: false,
      imgSrcShow: true,
      operationSrc: require("@/assets/2.png"),
      operationShow: false,
      overlaySrc: require("@/assets/overlay.png"),
      overlayLoadSrc: require("@/assets/overlay.gif"),
      overlayShow:false
    };
  },

  watch: {
    route(to, from) {
      console.log(to); //从哪来
      console.log(to.path); //到哪去
    },
  },
  mounted() {
    this.getCurrentTime();
  },
  methods: {
    getCurrentTime() {
      //获取当前时间并打印
      var _this = this;
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      _this.timeValue = hh + ":" + mf;
      _this.date = dd;
      _this.month = mm == 12 ? "Dec." : 12;
      _this.year = yy;
    },
    next(val) {
      if (val == "pageUp") {
        this.monthVal = "11月";
        this.month = "Nov.";
      } else if (val == "pageDown" && this.monthVal == "11月") {
        this.monthVal = "本月";
        this.month = "Dec.";
      } else if (val == "pageDown") {
        this.monthVal = "1月";
        this.month = "Jan.";
      }
    },
    double() {
      this.hdShow = !this.hdShow;
      this.hdShowBox = !this.hdShowBox;
      this.sdShow = false;
      this.sdShowBox = false;
    },
    christmas() {
      this.sdShow = !this.sdShow;
      this.sdShowBox = !this.sdShowBox;
      this.hdShow = false;
      this.hdShowBox = false;
    },
    dataWeChang() {
      this.imgSrcShow = !this.imgSrcShow;
      // this.$router.push({ path:"/Loaded"})
    },
    dataMoChang() {
      this.imgSrcShow = !this.imgSrcShow;
    },
    operationChangeVlaue(val) {
      this.operationShow = val;
    },
    getDetails() {
      this.$router.push({ path: "/Details" });
    },
  },
};
</script>
