<template>
  <div id="loading">
    <div class="header">
      <van-icon name="wap-nav" />
      <div class="tab">{{ timeValue }}</div>
    </div>
    <img :src="loadingSrc" alt="" />
    <div style="font-size: 3rem;font-family: extraLight;font-weight: 200;color: #5F5F5F;text-align: center;">加载中...</div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      loadingSrc: require("@/assets/loading.gif"),
      timeValue: "",
    };
  },
  mounted() {
    this.getCurrentTime();
  },
  methods: {
    getCurrentTime() {
      //获取当前时间并打印
      var _this = this;
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      _this.timeValue = hh + ":" + mf;
    },
  },
};
</script>
<style>
#loading {
  width: 600px;
  height: 1024px;
}
.tab {
  display: flex;
  justify-content: end;
  font-family: regular;
  font-size: 4rem;
  font-weight: 400;
  color: #434343;
  padding: 0;
  margin: 0 3rem 0;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .van-icon {
  padding: 0;
  margin: 0 3rem 0;
  font-size: 4rem;
}
</style>
