<template>
  <div>
    <div class="posterBg">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
    </div>
    <div class="front">
      <div>
        <div class="tab-top">
          <img :src="goBackSrc" width="3.3rem" height="3.3rem" alt="" @click="goBack"/>
          <span style="font-family: regular;font-weight: 400;"
            >{{ timeValue }}
          </span>
        </div>
        <div class="sw">
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item><img :src="images[0]" alt=""/></van-swipe-item>
            <van-swipe-item><img :src="images[1]" alt=""/></van-swipe-item>
            <van-swipe-item><img :src="images[2]" alt=""/></van-swipe-item>
            <van-swipe-item><img :src="images[3]" alt=""/></van-swipe-item>
            </van-swipe>
        </div>
      </div>

    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imgSrc: require("@/assets/hb.jpg"),
      timeValue: "",
      goBackSrc: require("@/assets/goBack.png"),
      images: [
        require("@/assets/hb1.jpg"),
        require("@/assets/hb2.jpg"),
        require("@/assets/hb3.jpg"),
        require("@/assets/hb4.jpg"),
      ],
    };
  },
  mounted() {
    this.getCurrentTime();
  },
  methods: {
    getCurrentTime() {
      var _this = this;
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      _this.timeValue = hh + ":" + mf;
    },
    goBack(){
        this.$router.push({ path: "/" });
    }
  },
};
</script>

